<template>
    <div class="main bg-primary">
        <v-card class="mx-auto loginCard mt-5" elevation="8" color="secondary">
            <form @submit.prevent="handleSubmit">
                <v-img class="align-center loginCover text-white align-top" height="250" cover :src="loginCoverImage"></v-img>

                <v-card-text class="cardForm">

                    <h1 class="mb-3">Vous nous avez manqué !</h1>


                    <v-text-field
                        clearable
                        label="Email"
                        placeholder="johndoe@gmail.com"
                        type="email"    
                        v-model="email"
                        variant="outlined"
                        rounded="lg"
                    ></v-text-field>

                    <v-text-field
                        label="Mot de passe"
                        :append-inner-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                        :rules="[rules.required, rules.min]"
                        :type="show1 ? 'text' : 'password'"
                        @click:append="show1 = !show1"
                        clearable
                        v-model="password"
                        hint="Entrez un mot de passe"
                        variant="outlined"
                        rounded="lg"
                        hide-details="true"
                    ></v-text-field>
                </v-card-text>

                <v-card-actions class="justify-center cardActions">

                    <v-btn rounded="lg" @click="login" size="large" type="submit" variant="elevated" class="text-white px-3 mb-1 fullWidth" color="#629c71">
                        Se connecter
                    </v-btn>
                    
                    <v-btn variant="text" size="small" to="#/register" class="mb-5">
                        Créer un compte
                    </v-btn>
                </v-card-actions>
            </form>
        </v-card>

        <v-snackbar v-if="errorMessage" v-model="errorMessage" color="error" top right>
            {{errorMessage}}
        </v-snackbar>
    </div>
</template>
  <script>
  import { getAuth, signInWithEmailAndPassword, GoogleAuthProvider, signInWithPopup } from "firebase/auth";
    export default {
      data: () => ({
        loginCoverImage: new URL('/assets/Images/loginBack.webp', import.meta.url).href,
        visible: false,
        email: "",
        password: "",
        errorMessage: "",
        show1: false,
        rules: {
        required: value => !!value || 'Required.',
          min: v => v.length >= 8 || 'Min 8 characters',
        },
      }),
      computed: {
          isValidEmail() {
              const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
              return emailRegex.test(this.forgottenEmail);
          },
      },
      methods: {
        login() {
          signInWithEmailAndPassword(getAuth(), this.email, this.password)
            .then(() => {
                this.$router.push('/');
            })
            .catch((error) => {
              const errorCode = error.code;
              const errorMessage = error.message;
                console.error(errorCode, errorMessage);
                console.log(error.code);
                switch (error.code){
                    case 'auth/user-disabled':
                        this.errorMessage = 'Utilisateur désactivé';
                        break;
                    case 'auth/user-not-found':
                        this.errorMessage = 'Utilisateur non trouvé';
                        break;
                    case 'auth/invalid-login-credentials':
                        this.errorMessage = 'Mot de passe ou adresse mail incorrect';
                        break;
                    default:    
                        this.errorMessage = 'Une erreur est survenue';
                        break;
                }
            });
        },
        loginWithGoogle() {
          const provider = new GoogleAuthProvider();
            signInWithPopup(getAuth(), provider)
                .then(() => {
                    this.$router.push('/')
                }).catch((error) => {
                    console.error(error);
                });
        }
      }
    }
  </script>

