<template>
    <loading-element v-if="authStore.isLoading"></loading-element>

    <div class="main bg-primary" v-else>
        <TopDeck v-if="authStore.userInfos" firstElement="Liste de" secondElement="courses" coloredSecond=true></TopDeck>

        <No_elements_create_new 
            title="Aucune liste de courses"
            description="Vous n'avez pas encore créé de liste de courses."
            :imageSrc="shoppingSvg"
            buttonText="Créer une nouvelle liste"
            :buttonAction="() => showDialog = true"
        />
    </div>

    <v-snackbar v-if="errorMessage" v-model="errorMessage" color="error" top right>
        {{ errorMessage }}
    </v-snackbar>

    <!-- Modal pour ajouter des éléments dans une liste de course -->
    <v-dialog v-model="showDialog" max-width="600px">
        <v-card>
            <v-card-text>
                <v-text-field v-model="listName" label="Nom de la liste" class="mb-4" />

                <div v-for="(item, index) in products" :key="index" class="d-flex align-center mb-2">
                    <div class="flex-grow-1">{{ item.name }} ({{ item.quantity }})</div>

                    <v-btn icon small @click="editProduct(index)">
                    <v-icon>mdi-pencil</v-icon>
                    </v-btn>

                    <v-btn icon small @click="removeProduct(index)">
                    <v-icon>mdi-delete</v-icon>
                    </v-btn>
                </div>

                <!-- Barre d'ajout en bas -->
                <div class="d-flex align-center mt-4">
                    <v-text-field
                        width="100%"
                        v-model="currentProduct.name"
                        label="Produit"
                        class="mr-2"
                        dense
                        hide-details
                    />
                    <v-text-field
                        v-model.number="currentProduct.quantity"
                        label="Qté"
                        type="number"
                        min="1"
                        dense
                        hide-details
                    />

                    <v-btn @click="isEditing ? updateProduct() : addProduct()" color="primary" class="ml-2">
                    {{ isEditing ? 'Modifier' : 'Ajouter' }}
                    </v-btn>
                </div>
            </v-card-text>
            <div class="d-flex justify-end p-4">
                <v-btn @click="showDialog = false" color="grey" class="mr-2">Annuler</v-btn>
                <v-btn @click="createShoppingList" color="primary">Créer</v-btn>
            </div>
        </v-card>
    </v-dialog>
</template>

<script>
import { ref } from "vue";
import LoadingElement from "../Animations/LoadingElement.vue";
import TopDeck from "../Utils/TopDeck.vue";
import { useAuthStore } from '@/store/auth_store';
import No_elements_create_new from "../Utils/NoElementsDisplayCreate.vue";
import shoppingSvg from '@/assets/SvgIcons/shopping.svg'

export default {
    name: 'ShoppingList',
    components: { LoadingElement, TopDeck, No_elements_create_new },
    setup() {
        const authStore = useAuthStore();
        const errorMessage = ref('');
        const showDialog = ref(false);
        const products = ref([]);
        const currentProduct = ref({ name: '', quantity: 1 });
        const isEditing = ref(false);
        const editingIndex = ref(null);
        const listName = ref('');

        const addProduct = () => {
            if (!currentProduct.value.name.trim()) return;
            products.value.push({ ...currentProduct.value });
            currentProduct.value = { name: '', quantity: 1 };
        };

        const editProduct = (index) => {
            currentProduct.value = { ...products.value[index] };
            editingIndex.value = index;
            isEditing.value = true;
        };

        const updateProduct = () => {
        if (editingIndex.value !== null) {
            products.value[editingIndex.value] = { ...currentProduct.value };
            isEditing.value = false;
            editingIndex.value = null;
            currentProduct.value = { name: '', quantity: 1 };
        }
        };

        const removeProduct = (index) => {
        products.value.splice(index, 1);
        };

        return {
            authStore,
            errorMessage,
            shoppingSvg,
            showDialog,
            products,
            currentProduct,
            isEditing,
            editingIndex,
            addProduct,
            editProduct,
            updateProduct,
            removeProduct,
            listName
        };
    }
}
</script>

