<template>
    <LoadingElement v-if="authStore.isLoading" />
    <div class="main bg-primary" v-else>
        <div class="rowSpaceBetween fullWidth">
            <TopDeck firstElement="Bonjour" :secondElement="authStore.userInfos?.username" coloredSecond="true"></TopDeck>
            <div class="noSpaceRowAction">
                <div class="flex-end mr-3">
                    <v-select
                        v-model="isDarkTheme"
                        :items="[
                            { text: 'Clair', value: false },
                            { text: 'Sombre', value: true }
                        ]"
                        item-title="text"
                        item-value="value"
                        label="Thème"
                        variant="outlined"
                        density="compact"
                        rounded="xl"
                        hide-details="true"
                        style="max-width: 200px; margin: 0 auto;"
                        @update:modelValue="toggleTheme($event)"
                    />
                </div>
                <v-btn v-if="authStore.isSignedIn" @click="handleSignout" color="error" rounded="xl" class="white-text-button">
                    <v-icon icon="mdi-power" class="mr-2"></v-icon>
                    Déconnexion
                </v-btn>
            </div>
        </div>
        <div class="myBasicInfosContainer">
            <div class="basicInfosHeader">
                <h2>Mes informations de base</h2>
                <v-btn color="accent" class="white-text-button" rounded="xl" size="small">
                    <v-icon icon="mdi-pencil" class="mr-2"></v-icon>
                    Modifier
                </v-btn>
            </div>
            <p><strong>Nom :</strong> {{ authStore.userInfos?.username }}</p>
            <p><strong>Email :</strong> {{ authStore.userInfos?.email }}</p>
        </div>
        <Info_display_bloc :user="authStore.userInfos" />
    </div>  

    <v-snackbar v-model="missingFields" color="error" :timeout="2000" location="bottom">
        Il manque des informations sur l'export, veuillez remplir tous les champs.
    </v-snackbar>
</template>

<script>
    import { ref, onMounted } from "vue";
    import { getAuth, signOut } from "firebase/auth";
    import TopDeck from '../Utils/TopDeck.vue';
    import { useTheme } from 'vuetify';
    import LoadingElement from "../Animations/LoadingElement.vue";
    import { useAuthStore } from '@/store/auth_store'
    import Info_display_bloc from "./InfoDisplayBlock.vue";

    export default {
        name: 'ProfilPage',
        components: { LoadingElement, TopDeck, Info_display_bloc },
        setup() {
            const auth = getAuth();
            const authStore = useAuthStore();
            const theme = useTheme();
            const isDarkTheme = ref(false);
            const missingFields = ref(false);

            const toggleTheme = (value) => {
                theme.global.name.value = value ? 'dark' : 'light';
                localStorage.setItem('theme', value ? 'dark' : 'light');
            };

            onMounted(() => {
                const savedTheme = localStorage.getItem('theme');
                if (savedTheme) {
                    isDarkTheme.value = savedTheme === 'dark';
                    theme.global.name.value = isDarkTheme.value ? 'dark' : 'light';
                }
            });

            const handleSignout = () => {
                signOut(auth)
                    .then(() => {
                        window.location.href = '/login';
                    })
                    .catch((error) => {
                        console.error(error);
                    });
            };

            return {
                authStore,
                isDarkTheme,
                toggleTheme,
                handleSignout,
                missingFields,
            };
        },
    }
</script>

