<template>
  <div class="main bg-primary">
    <div class="flex_row_between w60">
        <v-progress-linear
          color="accent"
          height="10"
          :model-value=step
          :max=getNumberOfSteps
          striped
        ></v-progress-linear>
        <div>
          <p class="text-white ml-6 flex_row">
            {{ step + 1 }} / {{ getNumberOfSteps }}
          </p>
        </div>
    </div>
    <div class="spacer"></div>
    <Register_question_block
      v-if="currentQuestion"
        :question="currentQuestion.question"
        :options="currentQuestion.options"
        :multipleChoice="currentQuestion.multipleChoice"
        :type="currentQuestion.type"
        :langKey="currentQuestion.key"
        :value="responses[currentQuestion.key]"
        @nextStep="nextStep"
        @previousStep="previousStep"
        @update="updateAnswer"
    />
  </div>
</template>

<script>

import { formStore } from '@/store/register_store';
import questions from '@/data/register_questions.json';
import Register_question_block from '../Utils/Account/register_question_block.vue';

export default {
  emits: ['nextStep', 'previousStep', 'update'],
  name: 'RegisterQuestions',
  components: { Register_question_block },
  data() {
    return {
      step: 0,
      questions,
      questionNumber: 0,
      responses: formStore.responses,
    };
  },
  watch: {
    step(newStep) {
      this.cleanMultipleChoiceResponses(newStep);
    },
  },
  computed: {
    currentQuestion() {
      return this.questions[this.step] || null;
    },
    getNumberOfSteps() {
      return this.questions.length;
    },
  },
  methods: {
    cleanMultipleChoiceResponses(currentStep) {
      const currentQ = this.questions[currentStep];
      if (currentQ?.multipleChoice) {
        if (!this.responses[currentQ.key] || Array.isArray(this.responses[currentQ.key])) {
          this.responses[currentQ.key] = [];
        }
      }
    },
    updateAnswer({ key, value }) {
      this.responses[key] = value;
    },
    nextStep() {
      if (this.step < this.questions.length - 1) {
        this.step++;
      } else {
        const allAnswered = this.questions.every(q => this.responses[q.key] !== undefined);
        if (allAnswered) {
          this.$router.push('/final_register');
        } else {
          alert('Veuillez répondre à toutes les questions !');
        }
      }
    },
    previousStep() {
      if (this.step > 0) this.step--;
    },
    mounted() {
      this.questions.forEach(q => {
        if (q.multipleChoice && !this.responses[q.key]) {
          this.responses[q.key] = [];
        }
      });
    },
  },
};
</script>

<style scoped>

  .w60 {
      width: 60%;
  }

  .spacer {
      margin-top: 2em;
      margin-bottom: 2em;
  }

  .main {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: flex-start;
  }
</style>
