<template>

<div class="main bg-primary"> 
        <v-card class="mx-auto RegisterCard mt-5" elevation="8" color="secondary">
            <form @submit.prevent="handleSubmit">
                <v-img class="align-center registerCover text-white align-top" height="250" cover :src="registerCoverImage"></v-img>

                <v-card-text class="cardForm">
                    <h1 class="mb-3">Entrez vos informations</h1>

                    <v-text-field
                        clearable
                        label="Pseudo"
                        placeholder="JohnDoe345"
                        type="text"    
                        required
                        v-model="username"
                        variant="outlined"
                        rounded="lg"
                    ></v-text-field>

                    <v-text-field
                        clearable
                        label="Mail"    
                        placeholder="johndoe@gmail.com"
                        type="email"    
                        required
                        v-model="email"
                        variant="outlined"
                        rounded="lg"
                    ></v-text-field>

                    <v-text-field
                        label="Mot de passe"
                        clearable
                        :rules="[rules.min]"
                        :append-inner-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                        :type="show1 ? 'text' : 'password'"
                        @click:append="show1 = !show1"
                        required
                        hint="Entrez un mot de passe"
                        v-model="password"
                        variant="outlined"
                        rounded="lg"
                    ></v-text-field>

                    <v-text-field
                        label="Confirmation du mot de passe"
                        clearable
                        required
                        :append-inner-icon="show2 ? 'mdi-eye' : 'mdi-eye-off'"
                        :type="show2 ? 'text' : 'password'"
                        @click:append="show2 = !show2"
                        v-model="passwordConfirmation"
                        :error="password !== passwordConfirmation"
                        :error-messages="password !== passwordConfirmation ? ['Les mots de passes ne correspondent pas !'] : []"
                        variant="outlined"
                        rounded="lg"
                        hide-details="true"
                    ></v-text-field>
                </v-card-text>
                <v-card-actions class="justify-center cardActions">
                    <v-btn rounded="lg" @click="register" variant="elevated" type="submit" class="text-white px-3 mb-1 fullWidth" color="#629c71"    
                     :disabled="password !== passwordConfirmation">
                        C'est parti !
                    </v-btn>

                    <v-btn variant="text" size="small" to="#/login" class="mb-5">
                        Déjà enregistré ? Connexion
                    </v-btn>
                </v-card-actions>
            </form>
        </v-card>

        <v-snackbar v-model="mailSnackBar" color="error" top right>
            Cet e-mail est déjà utilisé
        </v-snackbar>

        <v-snackbar v-model="phoneSnackBar" color="error" top right>
            Ce numéro de téléphone est déjà utilisé
        </v-snackbar>
    </div>
</template>

<script>
    import { formStore, isFormStoreEmpty } from '@/store/register_store';
    import { createUserWithEmailAndPassword, getAuth } from 'firebase/auth';
    import { addDoc, collection, getFirestore } from 'firebase/firestore';
    

    export default {
      data: () => ({
        registerCoverImage: new URL('/assets/Images/registerBack.webp', import.meta.url).href,
        show1: false,
        show2: false,
        mailSnackBar: false,
        phoneSnackBar: false,
        username: '',
        email: '',
        password: '',
        passwordConfirmation: '',
        rules: {
            min: v => !v || v.length >= 8 || 'Mininum 8 caractères',
        },
      }),
        methods: {
            register() {
                const auth = getAuth();
                const db = getFirestore();

                const userData = {
                    username: this.username,
                    email: this.email,
                    ...formStore.responses
                }

                createUserWithEmailAndPassword(auth, this.email, this.password)
                    .then((userCredential) => {
                    // Récupérer l'identifiant de l'utilisateur
                    const user = userCredential.user;
                    const userid = user.uid;
                    const username = this.username;
                    const email = this.email;

                    // Ajouter le tuple dans la collection "User" avec l'identifiant de l'utilisateur
                    const userRef = collection(db, "User");
                    addDoc(userRef, { userid, username, email, ...userData })
                        .then(() => {
                            this.$router.push('/'); 
                        })
                        .catch((error) => {
                            console.error("Erreur lors de l'ajout du tuple dans la collection 'User':", error);
                        });
                    })
                    .catch((error) => {
                        console.error(error);
                    });
            },
        },
        mounted() {
            //get users infos with formStore
            if(isFormStoreEmpty()) {
                //Not possible to register without answering the questions
                this.$router.push('/register');
            }
        },
    }
</script>

