<template>
  <v-app>
    <template v-if="authStore.isAuthReady">
      <component :is="authStore.isSignedIn ? 'NavBarUser' : 'NavBar'" />
    </template>
    <v-main>
      <router-view></router-view>
      <Footer />
    </v-main>
  </v-app>
</template>

<script>
import Footer from './components/Utils/FooterComp.vue'
import NavBar from './components/Utils/NavComp.vue'
import NavBarUser from './components/Utils/NavBarUser.vue'
import { onMounted } from 'vue';
import { useAuthStore } from '@/store/auth_store';
import { useThemeStore } from '@/store/theme_store';

export default {
  name: 'App',

  components: {
    Footer,
    NavBar,
    NavBarUser
  },

  setup() {
    const authStore = useAuthStore();
    const themeStore = useThemeStore();

    onMounted(() => {
      authStore.initAuth();
      themeStore.initTheme();
    });

    return {
      authStore,
      themeStore,
    };
  },
};
</script>

<style>
  @import "@/assets/css/global_style.css";

</style>
