<template>
  <loading-element v-if="authStore.isLoading" />

  <div class="main bg-primary" v-else>
    <TopDeck
      v-if="authStore.userInfos"
      firstElement="Ajouter"
      secondElement="un ingrédient"
      :coloredSecond="true"
      actionHeaderIcon="mdi-arrow-left"
      :actionheaderFunction="() => $router.push('/kitchen/my_ingredients')"
      actionHeaderColor="accent"
      actionHeaderTextColor="white"
    />

    <!-- ====== FLOW EXISTANT (un seul code-barres) ====== -->
    <v-text-field
      :label="$t('barcode')"
      variant="outlined"
      v-model="productInfo.barcode"
      clearable
      class="mb-3"
    />
    <v-btn color="accent" rounded="lg" @click="searchWithBarcode" class="white-text-button mb-6">
      <v-icon class="mr-1">mdi-magnify</v-icon>
      <span>Ajouter ingrédients</span>
    </v-btn>

    <v-card v-if="productInfo.foodInfo" class="pa-4 mb-8" elevation="4">
      <v-row>
        <v-col cols="12" md="3" class="ingredientImageContainer">
          <v-img :src="productInfo.productImage" width="100%" class="rounded-lg" contain />
        </v-col>

        <v-col cols="12" md="8">
          <h2 class="text-h5 font-weight-medium mb-2">{{ productInfo.productName }}</h2>
          <p class="text-subtitle-1 mb-2">{{ productInfo.productBrand }}</p>

          <v-chip
            v-if="productInfo.novaGroup.string"
            :color="productInfo.novaGroup.color"
            class="ma-1"
            label
            small
            outlined
          >
            {{ $t(productInfo.novaGroup.string) }}
          </v-chip>

          <div class="mt-1">
            <v-chip
              v-for="(label, index) in productInfo.labels"
              :key="'label-' + index"
              class="ma-1"
              color="black"
              label
              small
            >
              {{ label }}
            </v-chip>
          </div>

          <div class="mt-2">
            <v-chip
              v-for="(nutrient, index) in productInfo.nutriments_map"
              :key="'nutrient-' + index"
              class="ma-1"
              color="blue"
              text-color="white"
              label
              small
            >
              {{ $t(nutrient.key) }} : {{ nutrient.value }}
            </v-chip>
          </div>

          <div class="mt-4">
            <h3 class="text-subtitle-1 font-weight-medium">Allergènes :</h3>
            <v-chip
              v-for="(allergen, index) in productInfo.allergens"
              :key="'allergen-' + index"
              color="orange"
              class="ma-1"
              label
              small
            >
              {{ $t(allergen) }}
            </v-chip>
          </div>

          <div class="mt-4">
            <h3 class="text-subtitle-1 font-weight-medium">Additifs :</h3>
            <v-chip
              v-for="(additive, index) in productInfo.additives"
              :key="'additive-' + index"
              color="red"
              class="ma-1"
              label
              small
            >
              {{ additive }}
            </v-chip>
          </div>

          <div class="mt-4">
            <p v-if="productInfo.servingQuantity">Portion : {{ productInfo.servingQuantity }}g</p>
            <p v-if="productInfo.servingSize">Taille de portion : {{ productInfo.servingSize }}</p>
          </div>
        </v-col>
      </v-row>

      <v-btn color="accent" class="mt-4" @click="saveToCollection">
        Ajouter l'ingrédient
      </v-btn>
    </v-card>

    <!-- ====== NOUVEAU : IMPORT JSON DE L’EXTENSION + CARTES MULTIPLES ====== -->
    <div class="d-flex align-center mb-4">
      <v-btn color="primary" rounded="lg" class="white-text-button mr-3" @click="importFromExtension" :loading="isImporting">
        <v-icon class="mr-1">mdi-clipboard-arrow-down</v-icon>
        Récupérer les aliments de l'extension
      </v-btn>

      <v-spacer />

      <v-btn color="secondary" variant="text" @click="resetList" :disabled="items.length === 0 || isImporting">
        <v-icon class="mr-1">mdi-broom</v-icon>
        Vider la liste
      </v-btn>

      <v-btn color="accent" rounded="lg" class="ml-2" @click="addAllToCollection" :disabled="isPushing || itemsFoundCount === 0" :loading="isPushing">
        <v-icon class="mr-1">mdi-cloud-upload</v-icon>
        Ajouter tous ({{ itemsFoundCount }})
      </v-btn>
    </div>

    <!-- Récap entrées invalides -->
    <v-alert
      v-if="itemsNotFoundCount > 0"
      type="warning"
      variant="tonal"
      class="mb-6"
      title="Entrées non valides"
    >
      Certaines entrées du JSON ne contiennent pas les données minimales.
      <div class="mt-2">
        <v-chip
          v-for="(bc, i) in itemsNotFoundBarcodes"
          :key="bc + '-' + i"
          class="ma-1"
          color="warning"
          label
          small
        >
          {{ bc }}
        </v-chip>
      </div>
      <div class="mt-2">
        <v-btn size="small" variant="text" color="warning" @click="removeNotFound">
          <v-icon size="18" class="mr-1">mdi-close-circle</v-icon>
          Retirer ces entrées
        </v-btn>
      </div>
    </v-alert>

    <!-- Cartes multiples -->
    <v-row v-if="items.length">
      <v-col v-for="(it, idx) in items" :key="it.barcode + '-' + idx" cols="12" md="6" lg="4">
        <v-card class="pa-4" elevation="4" :disabled="it.status === 'not_found'">
          <template v-if="it.status === 'found'">
            <v-row>
              <v-col cols="12" md="4" class="ingredientImageContainer">
                <v-img :src="it.productImage" width="100%" class="rounded-lg" contain />
              </v-col>

              <v-col cols="12" md="8">
                <v-text-field v-model="it.productName" label="Nom du produit" variant="underlined" density="comfortable" />
                <v-text-field v-model="it.productBrand" label="Marque" variant="underlined" density="comfortable" />

                <div class="mt-1">
                  <v-chip
                    v-if="it.novaGroup?.string"
                    :color="it.novaGroup.color"
                    class="ma-1"
                    label
                    small
                    outlined
                  >
                    {{ $t(it.novaGroup.string) }}
                  </v-chip>

                  <v-chip v-for="(label, i) in it.labels" :key="'label-' + i" class="ma-1" color="black" label small>
                    {{ label }}
                  </v-chip>
                </div>

                <div class="mt-2">
                  <v-chip
                    v-for="(nutrient, i) in it.nutriments_map"
                    :key="'nut-' + i"
                    class="ma-1"
                    color="blue"
                    text-color="white"
                    label
                    small
                  >
                    {{ $t(nutrient.key) }} : {{ nutrient.value }}
                  </v-chip>
                </div>

                <div class="mt-3">
                  <v-text-field v-model="it.servingQuantity" label="Portion (g)" type="number" variant="underlined" density="comfortable" />
                  <v-text-field v-model="it.servingSize" label="Taille de portion" variant="underlined" density="comfortable" />
                  <v-text-field v-model="it.category" label="Catégorie (facultatif)" variant="underlined" density="comfortable" />
                </div>

                <div class="mt-3">
                  <h3 class="text-subtitle-1 font-weight-medium">Allergènes</h3>
                  <div class="mt-1">
                    <v-chip v-for="(a, i) in it.allergens" :key="'al-' + i" color="orange" class="ma-1" label small>
                      {{ $t(a) }}
                    </v-chip>
                  </div>

                  <h3 class="text-subtitle-1 font-weight-medium mt-3">Additifs</h3>
                  <div class="mt-1">
                    <v-chip v-for="(a, i) in it.additives" :key="'ad-' + i" color="red" class="ma-1" label small>
                      {{ a }}
                    </v-chip>
                  </div>
                </div>
              </v-col>
            </v-row>
          </template>

          <template v-else>
            <div class="d-flex align-center">
              <v-icon color="warning" class="mr-2">mdi-alert</v-icon>
              <div>
                <div class="text-subtitle-1">Entrée non valide</div>
                <div class="text-body-2 opacity-70">
                  Aucune donnée exploitable pour {{ it.barcode || '—' }}
                </div>
              </div>
            </div>
          </template>

          <v-divider class="my-4" />

          <div class="d-flex justify-space-between">
            <v-chip size="small" variant="tonal" class="mr-2" color="grey">{{ it.barcode }}</v-chip>

            <div class="d-flex align-center">
              <v-btn size="small" variant="text" color="error" @click="removeItem(idx)">
                <v-icon size="18" class="mr-1">mdi-delete</v-icon>
                Supprimer
              </v-btn>
            </div>
          </div>
        </v-card>
      </v-col>
    </v-row>
  </div>

  <v-snackbar v-model="snackbar" :color="snackbarColor" top right timeout="3500">
    {{ errorMessage || snackbarMessage }}
  </v-snackbar>
</template>

<script>
import { ref, computed } from "vue";
import { getNovaStringAndColor, addIngredientToUserCollection, addIngredientsBulkToUserCollection } from '@/service/food_service';
import { getFoodInfoWithBarcode } from '@/assets/js/gameSearchJs';
import LoadingElement from "@/components/Animations/LoadingElement.vue";
import TopDeck from "@/components/Utils/TopDeck.vue";
import { useAuthStore } from '@/store/auth_store';

export default {
  name: 'AddIngredientPage',
  components: { LoadingElement, TopDeck },
  setup() {
    const authStore = useAuthStore();

    // ====== état existant
    const snackbar = ref(false);
    const errorMessage = ref('');
    const productInfo = ref({
      barcode: '8000500110683',
      foodInfo: null,
      productName: '',
      productImage: '',
      productBrand: '',
      novaGroup: { string: '', color: '' },
      labels: [],
      nutriments_map: [],
      servingQuantity: null,
      servingSize: null,
      allergens: [],
      additives: [],
    });

    // ====== nouvel état pour les cartes multiples
    const items = ref([]); // [{status:'found'|'not_found', barcode, ...}]
    const isImporting = ref(false);
    const isPushing = ref(false);
    const snackbarMessage = ref('');
    const snackbarColor = ref('success');

    const itemsFoundCount = computed(() => items.value.filter(i => i.status === 'found').length);
    const itemsNotFoundCount = computed(() => items.value.filter(i => i.status === 'not_found').length);
    const itemsNotFoundBarcodes = computed(() => items.value.filter(i => i.status === 'not_found').map(i => i.barcode || '—'));

    const showSnack = (msg, color = 'success') => { snackbarMessage.value = msg; snackbarColor.value = color; snackbar.value = true; };

    // ====== helpers communs
    const extractNutrimentsFromJson = (nutriments) => {
      const nutrimentsArray = [];
      const importantKeys = ['energy-kcal', 'fat', 'saturated-fat', 'sugars', 'salt', 'proteins', 'carbohydrates', 'fiber'];
      for (const key in nutriments || {}) {
        if (key.endsWith('_100g')) {
          const name = key.replace('_100g', '');
          if (!importantKeys.includes(name)) continue;
          const value = nutriments[key];
          const unit = nutriments[`${name}_unit`] || 'g';
          nutrimentsArray.push({ key: name, value: `${value} ${unit}` });
        }
      }
      return nutrimentsArray;
    };

    const cleanInfos = () => {
      productInfo.value.foodInfo = null;
      productInfo.value.productName = '';
      productInfo.value.productImage = '';
      productInfo.value.productBrand = '';
      productInfo.value.novaGroup = { string: '', color: '' };
      productInfo.value.labels = [];
      productInfo.value.nutriments_map = [];
      productInfo.value.servingQuantity = null;
      productInfo.value.servingSize = null;
      productInfo.value.allergens = [];
      productInfo.value.additives = [];
    };

    // ====== flux existant : recherche par code-barres (conservé)
    const searchWithBarcode = () => {
      if (!productInfo.value.barcode) {
        errorMessage.value = "Veuillez entrer un code-barres valide.";
        snackbar.value = true;
        return;
      }
      cleanInfos();
      getFoodInfoWithBarcode(productInfo.value.barcode).then(product => {
        if (product) {
          productInfo.value.foodInfo = product;
          productInfo.value.productImage = product.image_front_url || 'https://via.placeholder.com/150';
          productInfo.value.productBrand = product.brands || 'Marque non disponible';
          productInfo.value.productName = product.product_name_fr || 'Nom non disponible';
          productInfo.value.novaGroup = getNovaStringAndColor(product.nova_group);
          productInfo.value.labels = product.labels ? [product.labels] : [];
          productInfo.value.nutriments_map = extractNutrimentsFromJson(product.nutriments || {});
          productInfo.value.servingQuantity = product.serving_quantity || null;
          productInfo.value.servingSize = product.serving_size || null;
          productInfo.value.allergens = (product.allergens_tags || []).map(tag => tag.split(':')[1]);
          productInfo.value.additives = (product.additives_tags || []).map(tag => tag.split(':')[1]);
        }
      });
    };

    const saveToCollection = () => {
      if (!productInfo.value.foodInfo) {
        errorMessage.value = "Aucune information sur l'ingrédient à ajouter.";
        snackbar.value = true;
        return;
      }

      const ingredientId = productInfo.value.barcode;

      const ingredientData = {
        productName: productInfo.value.productName,
        productImage: productInfo.value.productImage,
        productBrand: productInfo.value.productBrand,
        novaGroup: productInfo.value.novaGroup,
        labels: productInfo.value.labels,
        nutriments_map: productInfo.value.nutriments_map,
        servingQuantity: productInfo.value.servingQuantity,
        servingSize: productInfo.value.servingSize,
        allergens: productInfo.value.allergens,
        additives: productInfo.value.additives,
        favorite: false,
        userId: authStore.userInfos.userid,
        added_dt: new Date().toISOString(),
        category: '',
      };

      addIngredientToUserCollection(authStore.userInfos.userid, ingredientId, ingredientData)
        .then((res) => {
          if (res.alreadyExists) {
            errorMessage.value = "L'ingrédient est déjà présent dans votre liste.";
            snackbar.value = true;
          } else {
            errorMessage.value = "Ingrédient ajouté avec succès !";
            cleanInfos();
            snackbar.value = true;
          }
        })
        .catch(error => {
          console.error("Erreur lors de l'ajout de l'ingrédient :", error);
          errorMessage.value = "Erreur lors de l'ajout de l'ingrédient.";
          snackbar.value = true;
        });
    };

    // ====== NOUVEAU : import JSON depuis l'extension
    const toItemFromSlim = (slim) => {
      if (!slim || !slim.barcode) {
        return { barcode: slim?.barcode ?? '—', status: 'not_found' };
      }
      return {
        barcode: slim.barcode,
        status: 'found',
        productImage: slim.productImage || 'https://via.placeholder.com/150',
        productBrand: slim.productBrand || 'Marque non disponible',
        productName: slim.productName || 'Nom non disponible',
        novaGroup: getNovaStringAndColor(slim.nova_group),
        labels: slim.labels || [],
        nutriments_map: extractNutrimentsFromJson(slim.nutriments || {}),
        servingQuantity: slim.serving_quantity || null,
        servingSize: slim.serving_size || null,
        allergens: (slim.allergens_tags || []).map(t => (typeof t === 'string' && t.includes(':') ? t.split(':')[1] : t)),
        additives: (slim.additives_tags || []).map(t => (typeof t === 'string' && t.includes(':') ? t.split(':')[1] : t)),
        favorite: false,
        category: ''
      };
    };

    const importFromExtension = async () => {
      try {
        isImporting.value = true;
        const text = await navigator.clipboard.readText();
        if (!text) { showSnack("Presse-papiers vide.", "warning"); return; }

        let payload = null;
        try { payload = JSON.parse(text); }
        catch { showSnack("Le presse-papiers ne contient pas un JSON valide.", "error"); return; }

        if (!payload || payload.source !== "off-extension" || !Array.isArray(payload.items)) {
          showSnack("Format JSON inattendu (source inconnue).", "error");
          return;
        }

        const mapped = payload.items.map(toItemFromSlim);

        // dédup par barcode (le dernier l'emporte)
        const byBc = new Map();
        for (const it of mapped) byBc.set(it.barcode, it);
        items.value = Array.from(byBc.values());

        const found = itemsFoundCount.value;
        const miss  = items.value.length - found;
        if (found) showSnack(`${found} produit(s) importé(s).`, "success");
        if (miss)  showSnack(`${miss} entrée(s) non valide(s).`, "warning");
      } catch (e) {
        console.error(e);
        showSnack("Erreur pendant l'import.", "error");
      } finally {
        isImporting.value = false;
      }
    };

    // ====== gestion cartes multiples
    const removeItem = (index) => { items.value.splice(index, 1); };
    const removeNotFound = () => { items.value = items.value.filter(i => i.status === 'found'); };
    const resetList = () => { items.value = []; };

    // // ====== push Firestore pour toutes les cartes
    // const buildIngredientData = (it) => ({
    //   productName: it.productName,
    //   productImage: it.productImage,
    //   productBrand: it.productBrand,
    //   novaGroup: it.novaGroup,
    //   labels: it.labels,
    //   nutriments_map: it.nutriments_map,
    //   servingQuantity: it.servingQuantity,
    //   servingSize: it.servingSize,
    //   allergens: it.allergens,
    //   additives: it.additives,
    //   favorite: false,
    //   userId: authStore.userInfos.userid,
    //   added_dt: new Date().toISOString(),
    //   category: it.category || '',
    // });

    const addAllToCollection = async () => {
        if (!authStore?.userInfos?.userid) { showSnack("Utilisateur non authentifié.", "error"); return; }
        const foundItems = items.value.filter(i => i.status === 'found');
        if (!foundItems.length) { showSnack("Aucun produit à ajouter.", "warning"); return; }

        isPushing.value = true;
        try {
            // Construire la map { [barcode]: ingredientData }
            const payload = {};
            for (const it of foundItems) {
            payload[it.barcode] = {
                productName: it.productName,
                productImage: it.productImage,
                productBrand: it.productBrand,
                novaGroup: it.novaGroup,
                labels: it.labels,
                nutriments_map: it.nutriments_map,
                servingQuantity: it.servingQuantity,
                servingSize: it.servingSize,
                allergens: it.allergens,
                additives: it.additives,
                favorite: false,
                userId: authStore.userInfos.userid,
                added_dt: new Date().toISOString(),
                category: it.category || '',
            };
            }

            const { added, already } = await addIngredientsBulkToUserCollection(authStore.userInfos.userid, payload);

            if (added > 0)  showSnack(`${added} ajouté(s) avec succès.`, "success");
            if (already > 0) showSnack(`${already} déjà présent(s).`, "info");
            if (added === 0 && already === 0) showSnack("Rien à ajouter.", "info");
        } catch (e) {
            console.error(e);
            showSnack("Erreur lors de l'ajout en lot.", "error");
        } finally {
            isPushing.value = false;
        }
    };


    return {
      authStore,
      // existant
      productInfo, errorMessage, snackbar, searchWithBarcode, saveToCollection,
      // nouveau
      items, isImporting, isPushing, importFromExtension,
      removeItem, removeNotFound, resetList, addAllToCollection,
      itemsFoundCount, itemsNotFoundCount, itemsNotFoundBarcodes,
      snackbarMessage, snackbarColor
    };
  }
}
</script>

<style scoped>
.ingredientImageContainer {
  display: flex;
  justify-content: center;
  align-items: center;
}
.opacity-70 { opacity: .7; }
</style>
