import { createApp } from 'vue'
import App from './App.vue'
import vuetify from './plugins/vuetify'
import { loadFonts } from './plugins/webfontloader'
import HomePage from './components/Home/HomePage.vue'
import Login from './components/Account/LoginComp.vue'
import Register from './components/Account/RegisterComp.vue'
import Profile from './components/Account/ProfilPage.vue'
import ShoppingList from './components/ShoppingList/ShoppingListPage.vue'
import MenuPage from './components/Menus/MenuPage.vue'
import MyIngredients from './components/Kitchen/Ingredients/MyIngredients.vue'
import WelcomePage from './components/Utils/WelcomePage.vue'
import Register_questions from './components/Account/RegisterQuestion.vue'
import My_kitchen from './components/Kitchen/MyKitchen.vue'
import AddIngredients from './components/Kitchen/Ingredients/AddIngredients.vue'

import { createRouter, createWebHashHistory } from 'vue-router';
import i18n from './i18n'
import { createPinia } from 'pinia'


import { initializeApp } from "firebase/app";

const firebaseConfig = {
  apiKey: process.env.VUE_APP_FIREBASE_API_KEY,
  authDomain: process.env.VUE_APP_FIREBASE_AUTH_DOMAIN,
  projectId: process.env.VUE_APP_FIREBASE_PROJECT_ID,
  storageBucket: process.env.VUE_APP_FIREBASE_STORAGE_BUCKET,
  messagingSenderId: process.env.VUE_APP_FIREBASE_MESSAGING_SENDER_ID,
  appId: process.env.VUE_APP_FIREBASE_APP_ID,
  measurementId: process.env.VUE_APP_FIREBASE_MEASUREMENT_ID
};

initializeApp(firebaseConfig);

loadFonts()

import { getAuth, onAuthStateChanged } from "firebase/auth";

const routes = [ 
  { path: '/login', name: "login", component: Login },
  { path: '/register', name: "register", component: Register },
  { path: '/final_register', name: "final_register", component: Register },
  { path: '/register', name: "register", component: Register_questions },

  // Requiring authentication
  { path: '/welcome', name: "welcome", component: WelcomePage, meta: { requiresAuth: false }},
  { path: '/', name: "home", component: HomePage, meta: { requiresAuth: true, breadcrumb: 'Accueil' } },
  { path: '/profile', name: "profile", component: Profile, meta: { requiresAuth: true, breadcrumb: 'Mon profil' }},
  { path: '/shopping_list', name: "ShoppingList", component: ShoppingList, meta: { requiresAuth: true, breadcrumb: 'Mes listes' }},
  { path: '/menu', name: "menu", component: MenuPage, meta: { requiresAuth: true, breadcrumb: 'Mes menus' }},
  { path: '/kitchen', name: "kitchen", component: My_kitchen, meta: { requiresAuth: true, breadcrumb: 'Ma cuisine' }},
  { path: '/kitchen/my_ingredients', name: "my_ingredients", component: MyIngredients, meta: { requiresAuth: true, breadcrumb: 'Mes ingrédients' }},
  { path: '/kitchen/add_ingredient', name: "add_ingredient", component: AddIngredients, meta: { requiresAuth: true, breadcrumb: 'Ajouter un ingrédient' }},
];

const getCurrentUser = () => {
  return new Promise((resolve, reject) => {
    const removeListener = onAuthStateChanged(getAuth(), (user) => {
      removeListener()
      resolve(user)
    },
    reject,
    );
  });
};
  
const router = createRouter({
  history: createWebHashHistory('/'),
  routes,
  scrollBehavior() {
    return { top: 0 }
  },
});

router.beforeEach(async (to, from, next) => {
  if (to.matched.some(record => record.meta.requiresAuth)) {
    if (await getCurrentUser()) {
      next()
    } else {
      next({ path: '/welcome' })
    }
  } else {
    if ((to.path === '/register' || to.path === '/login') && await getCurrentUser()) {
      next({ path: '/' }) 
    } else {
      next() 
    }
  }
})

createApp(App)
  .use(vuetify)
  .use(router)
  .use(i18n)
  .use(createPinia())
  .mount('#app')
