import { doc, setDoc, getDoc } from "firebase/firestore";
import { db } from "@/firebase";

export async function addIngredientToUserCollection(userId, ingredientId, ingredientData) {
  const docRef = doc(db, "Ingredients", `${userId}-ingredient`);

  try {
    const docSnap = await getDoc(docRef);
    const existingData = docSnap.exists() ? docSnap.data() : {};

    if (Object.prototype.hasOwnProperty.call(existingData, ingredientId)) {
      console.log("⚠️ L'ingrédient est déjà présent.");
      return { alreadyExists: true };
    }

    const updatedData = {
      ...existingData,
      [ingredientId]: ingredientData,
    };

    await setDoc(docRef, updatedData);
    console.log("✅ Ingrédient ajouté avec succès.");
    return { alreadyExists: false };
  } catch (error) {
    console.error("❌ Erreur lors de l'ajout de l'ingrédient :", error);
    throw error;
  }
}

/**
 * Ajoute un lot d'ingrédients sans écraser le document complet.
 * - Calcule ce qui existe déjà vs ce qui est nouveau
 * - N'écrit que les nouveaux (merge: true)
 * - Retourne { added, already }
 */
export async function addIngredientsBulkToUserCollection(userId, entriesMap) {
  const docRef = doc(db, "Ingredients", `${userId}-ingredient`);

  // 1) Lire l'existant
  const snap = await getDoc(docRef);
  const existing = snap.exists() ? snap.data() : {};
  const existingIds = new Set(Object.keys(existing));

  // 2) Séparer nouveaux / déjà présents
  const incomingIds = Object.keys(entriesMap || {});
  const toInsertIds = incomingIds.filter((id) => !existingIds.has(id));
  const alreadyIds  = incomingIds.filter((id) =>  existingIds.has(id));

  // 3) Si nouveaux, écrire uniquement ceux-là
  if (toInsertIds.length > 0) {
    const payload = {};
    for (const id of toInsertIds) {
      payload[id] = entriesMap[id];
    }
    await setDoc(docRef, payload, { merge: true });
  }

  return {
    added: toInsertIds.length,
    already: alreadyIds.length,
  };
}

/**
 * Supprime un ingrédient de la collection d'un utilisateur.
 * @param {string} userId - L'ID de l'utilisateur.
 * @param {string} ingredientId - L'ID de l'ingrédient à supprimer.
 */
export async function deleteIngredientFromUserCollection(userId, ingredientId) {
  const docRef = doc(db, "Ingredients", `${userId}-ingredient`);

  try {
    const docSnap = await getDoc(docRef);
    if (!docSnap.exists()) {
      console.warn("⚠️ Aucun document trouvé.");
      return;
    }

    const data = docSnap.data();
    if (!Object.prototype.hasOwnProperty.call(data, ingredientId)) {
      console.warn("⚠️ L'ingrédient n'existe pas dans la collection.");
      return;
    }

    delete data[ingredientId];

    await setDoc(docRef, data);
    console.log("🗑️ Ingrédient supprimé avec succès.");
  } catch (error) {
    console.error("❌ Erreur lors de la suppression de l'ingrédient :", error);
    throw error;
  }
}

/**
 * Met à jour un ingrédient en le passant en favori (favoris = true).
 * @param {string} userId - L'ID de l'utilisateur.
 * @param {string} barcode - Le code barre (clé de l'ingrédient).
 */
export async function setIngredientAsFavorite(userId, barcode, isFavorite = true) {
  const docRef = doc(db, "Ingredients", `${userId}-ingredient`);

  try {
    const docSnap = await getDoc(docRef);
    if (!docSnap.exists()) return;

    const data = docSnap.data();
    if (!Object.prototype.hasOwnProperty.call(data, barcode)) return;

    const updatedIngredient = {
      ...data[barcode],
      favorite: isFavorite, // ← mise à jour ici
    };

    await setDoc(docRef, {
      ...data,
      [barcode]: updatedIngredient,
    });

    console.log(`⭐ Favori mis à jour pour ${barcode} : ${isFavorite}`);
  } catch (error) {
    console.error("❌ Erreur favori :", error);
    throw error;
  }
}


/**
 * Récupère les ingrédients d'un utilisateur.
 * @param {string} userId - L'ID de l'utilisateur.
 * @returns {Promise<Object>} Une map d'ingrédients ou un objet vide.
 */
export async function getUserIngredients(userId) {
  const docRef = doc(db, "Ingredients", `${userId}-ingredient`);

  try {
    const docSnap = await getDoc(docRef);

    if (!docSnap.exists()) {
      console.log("📭 Aucun ingrédient trouvé.");
      return {};
    }

    const data = docSnap.data();
    console.log("📦 Ingrédients récupérés :", data);
    return data;
  } catch (error) {
    console.error("❌ Erreur lors de la récupération des ingrédients :", error);
    throw error;
  }
}


// Groupe 1 - Aliments non transformés ou transformés minimalement
// Groupe 2 - Ingrédients culinaires transformés
// Groupe 3 - Aliments transformés
// Groupe 4 - Produits alimentaires et boissons ultra-transformés
export function getNovaStringAndColor(novaGroup) {
  let color = 'grey';

  switch (novaGroup) {
    case 1:
      color = 'green'; break;
    case 2:
      color = 'yellow'; break;
    case 3:
      color = 'orange'; break;
    case 4:
      color = 'red'; break;
  }

  return {
    string: `nova.${novaGroup}`, // clef à passer à $t()
    color
  };
}

