<template>
  <loading-element v-if="authStore.isLoading" />
  <div class="main bg-primary" v-else>
    <TopDeck
      v-if="authStore.userInfos"
      firstElement="Mes"
      secondElement="ingrédients"
      :coloredSecond="true"
    />

    <v-row class="mt-6" no-gutters>
      <!-- Liste d’ingrédients -->
      <v-col cols="12" md="9" class="pr-2">
        <div class="pa-4 bg-white rounded-lg">
          <h3 class="text">Liste d’ingrédients</h3>

          <div v-if="isLoadingIngredients" class="d-flex justify-center my-10">
            <v-progress-circular indeterminate color="primary" />
          </div>

          <div v-else>
            <!-- Recherche + bouton -->
            <div class="d-flex align-center my-4" style="gap: 1rem;">
              <v-text-field
                v-model="searchQuery"
                placeholder="Rechercher"
                hide-details
                density="compact"
                variant="tonal"
                prepend-inner-icon="mdi-magnify"
                class="searchField rounded-lg"
                rounded="lg"
              />
              <v-btn
                color="accent"
                prepend-icon="mdi-plus"
                elevation="0"
                rounded="lg"
                @click="router.push('/kitchen/add_ingredient')"
              >
                Ajouter ingrédient
              </v-btn>
            </div>

            <!-- Tableau -->
            <v-table density="compact">
              <thead>
                <tr>
                  <th>Ingrédient</th> <!-- icône + image -->
                  <th>Nom</th>
                  <th>Catégorie</th>
                  <th>Marque</th>
                  <th>Portion (g)</th>
                </tr>
              </thead>
              <tbody>
                <tr
                  v-for="item in filteredIngredients"
                  :key="item.id"
                  @click="select(item)"
                  class="ingredients"
                >
                  <td class="d-flex align-center">
                    <v-icon
                      :color="item.favorite ? 'orange' : 'grey lighten-1'"
                      class="mr-2"
                      size="18"
                    >
                      mdi-star
                    </v-icon>
                    <v-img
                      :src="item.productImage"
                      width="32"
                      height="32"
                      fit
                    />
                  </td>
                  <td>{{ item.productName }}</td>
                  <td>{{ item.category }}</td>
                  <td>{{ item.productBrand ?? 'Inconnu' }}</td>
                  <td>{{ item.servingQuantity }} g</td>
                </tr>
              </tbody>
            </v-table>
          </div>
        </div>
      </v-col>

      <!-- Fiche ingrédient -->
      <v-col cols="12" md="3" class="bg-secondary pa-4 rounded-lg">
        <div v-if="selectedIngredient">
          <h4 class="font-weight-medium mb-4">Fiche ingrédient</h4>
          <v-img :src="selectedIngredient.image" width="120" class="mb-2" />
          <p class="mb-2 font-weight-medium">{{ selectedIngredient.productName }}</p>

          <div v-if="selectedIngredient.nutriments_map">
            <h4 class="mt-4">Nutriments</h4>
            <ul class="nutriment-list">
              <p
                v-for="(item, index) in selectedIngredient.nutriments_map"
                :key="index"
                class="nutriment-line"
              >
                <span>{{ $t(item.key) }}</span>
                <span>{{ item.value }}</span>
              </p>
            </ul>
          </div>

          <div v-if="selectedIngredient.allergens && selectedIngredient.allergens.length">
            <p class="mb-2 font-weight-medium">Allergènes :</p>
            <div class="allergen-list">
              <v-chip
                v-for="(allergen, index) in selectedIngredient.allergens"
                :key="index"
                color="blue lighten-2"
                text-color="white"
                class="ma-1"
                label
                small
              >
                {{ $t(allergen) }}
              </v-chip>
            </div>
          </div>
          <div v-else>
            <p class="text-grey">Aucun allergène</p>
          </div>

          <p class="text-red mt-2" v-if="selectedIngredient.novaGroup?.string">
            <v-chip :color="selectedIngredient.novaGroup.color">
              {{ $t(selectedIngredient.novaGroup.string) }}
            </v-chip>
          </p>
          <v-btn block class="my-2" rounded="lg" elevation="0">Exclure des recettes</v-btn>
          <v-btn block color="black" class="my-2" rounded="lg" elevation="0" @click="deleteSelectedIngredient">
            Supprimer
          </v-btn>

          <v-btn block :color="selectedIngredient.favorite ? 'orange' : 'purple'" rounded="lg" variant="tonal" @click="toggleFavorite">
            {{ selectedIngredient.favorite ? 'Retirer des favoris' : 'Favoris' }}
          </v-btn>
        </div>
        <div v-else>
          <p class="text-grey">Sélectionnez un ingrédient pour voir le détail.</p>
        </div>
      </v-col>
    </v-row>

    <v-snackbar v-if="errorMessage" color="error" timeout="3000" top right>
      {{ errorMessage }}
    </v-snackbar>
  </div>
</template>

<script>
import { ref, computed, onMounted, watch } from "vue";
import { useRouter } from "vue-router";
import LoadingElement from "@/components/Animations/LoadingElement.vue";
import TopDeck from "@/components/Utils/TopDeck.vue";
import { useAuthStore } from "@/store/auth_store";
import { getUserIngredients, deleteIngredientFromUserCollection, setIngredientAsFavorite, } from "@/service/food_service";

export default {
  name: "MyIngredients",
  components: { LoadingElement, TopDeck },
  setup() {
    const authStore = useAuthStore();
    const errorMessage = ref("");
    const router = useRouter();

    const ingredients = ref([]);
    const isLoadingIngredients = ref(false);
    const selectedIngredient = ref(null);
    const searchQuery = ref("");

    const filteredIngredients = computed(() => {
      const q = searchQuery.value.trim().toLowerCase();
      return ingredients.value.filter((item) => {
        return ['productName', 'productBrand', 'category'].some((key) => {
          return (item[key] ?? '').toLowerCase().includes(q);
        });
      });
    });

    function select(item) {
      selectedIngredient.value = item;
    }

    async function deleteSelectedIngredient() {
      if (!selectedIngredient.value || !authStore.userInfos?.userid) return;

      try {
        await deleteIngredientFromUserCollection(authStore.userInfos.userid, selectedIngredient.value.id);

        ingredients.value = ingredients.value.filter(
          (item) => item.id !== selectedIngredient.value.id
        );

        selectedIngredient.value = null;
      } catch (err) {
        console.error("❌ Erreur suppression :", err);
        errorMessage.value = "Erreur lors de la suppression de l'ingrédient.";
      }
    }

    async function toggleFavorite() {
      if (!selectedIngredient.value || !authStore.userInfos?.userid) return;

      try {
        const id = selectedIngredient.value.id;
        const current = ingredients.value.find((i) => i.id === id);
        if (!current) return;

        const newValue = !current.favorite;

        await setIngredientAsFavorite(authStore.userInfos.userid, id, newValue);

        // met à jour la valeur dans la liste ET la fiche
        current.favorite = newValue;
        selectedIngredient.value.favorite = newValue;
      } catch (err) {
        console.error("❌ Erreur favoris :", err);
        errorMessage.value = "Erreur lors de la mise à jour du favori.";
      }
    }

    onMounted(() => {
      watch(
        () => authStore.userInfos,
        async (newUserInfos) => {
          if (newUserInfos?.userid) {
            try {
              isLoadingIngredients.value = true;
              const data = await getUserIngredients(newUserInfos.userid);
              ingredients.value = Object.entries(data).map(([id, value]) => ({
                id,
                ...value,
              }));
            } catch (error) {
              console.error("Erreur récupération ingrédients:", error);
              errorMessage.value = "Erreur lors du chargement des ingrédients.";
            } finally {
              isLoadingIngredients.value = false;
            }
          }
        },
        { immediate: true }
      );
    });

    return {
      authStore,
      errorMessage,
      router,
      ingredients,
      selectedIngredient,
      select,
      searchQuery,
      filteredIngredients,
      isLoadingIngredients,
      deleteSelectedIngredient,
      toggleFavorite,
    };
  },
};
</script>

<style scoped>

  .nutriment-line {
    display: flex;
    justify-content: space-between;
    padding: 4px 0;
    border-bottom: 1px solid rgba(0, 0, 0, 0.05);
  }

  .ingredients:hover {
    cursor: pointer;
    background-color: rgba(0, 0, 0, 0.05);
  }

  .searchField {
    background-color: rgb(244, 244, 244);
  }
</style>
