<template>
  <div class="mainQuestionContainer bg-secondary">
    <h2 class="text-2xl font-semibold mb-2">{{ $t(langKey) }}</h2>

    <!-- Affichage des types -->
    <div class="">
        <input
            v-if="type === 'text' || type === 'number'"
            v-model="valueLocal"
            :type="type"
            class="inputAnswer"
            placeholder="Entrez votre réponse" 
        />

        <!-- Choix d’options (boutons cliquables) -->
        <div v-else-if="type === 'checkbox' || type === 'radio'" class="optionsContainer">
                <div
                    v-for="(opt, i) in options"
                        :key="i"
                        :class="{
                            'option bg-tertiary borderAccent': multipleChoice ? valueLocal.includes(opt.option) : valueLocal === opt.option,
                            'option bg-tertiary': multipleChoice ? !valueLocal.includes(opt.option) : valueLocal !== opt.option
                        }"
                        @click="selectOption(opt.option)"
                    >
                <span class="text-left font-medium">{{ $t(opt.option) }}</span>
            </div>
        </div>
    </div>

    <!-- Navigation -->
    <div class="mt-5 actionContainer">
      <button v-if="question > 1" @click="previousStep" class="previousButton">Retour</button>
      <button v-if="(multipleChoice && type !== 'text' && type !== 'number') || type === 'number' || type === 'text'" @click="submit" class="nextButton bg-accent">Suivant</button>
    </div>
  </div>


    <v-snackbar v-if="errorMessage" v-model="errorMessage" color="error" top right>
        {{ errorMessage }}
    </v-snackbar>
</template>

<script>
export default {
    name: 'RegisterQuestionsBlock',
    props: {
        langKey: String,
        question: String,
        options: Array,
        type: String,
        multipleChoice: Boolean,
        value: [String, Number, Array],
    },
    data() {
        return {
            valueLocal: this.multipleChoice ? (this.value || []) : (this.value || ''),
            errorMessage: '',
        };
    },
    computed: {
        showHelperText() {
        return this.type === 'select';
        }
    },
    watch: {
        value(newVal) {
            this.valueLocal = this.multipleChoice ? (newVal || []) : (newVal || '');
        },
    },
    mounted() {
        this.valueLocal = this.multipleChoice ? (this.value || []) : (this.value || '');
    },
    methods: {
        submit() {
            if (!this.valueLocal) {
                this.errorMessage = 'Veuillez répondre à la question';
            } else {
                this.$emit('update', { key: this.langKey, value: this.valueLocal });
                if(!this.multipleChoice && this.type !== 'text' && this.type !== 'number') {
                    //add a delay to allow the UI to update before emitting nextStep
                    setTimeout(() => {
                        this.$emit('nextStep');
                    }, 200);
                } else {
                    this.$emit('nextStep');
                }
                this.errorMessage = '';
            }
            
        },
        selectOption(option) {
            if (this.type === 'checkbox') {
                if (!Array.isArray(this.valueLocal)) this.valueLocal = [];
                const index = this.valueLocal.indexOf(option);
                if (index > -1) {
                this.valueLocal.splice(index, 1);
                } else {
                this.valueLocal.push(option);
                }
            } else {
                this.valueLocal = option;
                if (!this.multipleChoice) this.submit();
            }
        },
        previousStep() {
            this.$emit('previousStep');
        },
    },
};
</script>

<style>
  .option:hover {
    border: rgb(var(--v-theme-accent)) 3px solid !important;
    cursor: pointer;
  }

  .borderAccent {
    border: 3px solid rgb(var(--v-theme-accent)) !important;
  }
</style>

<style scoped>

    .mainQuestionContainer {
        padding: 2em;
        width: 30%;
        display: flex;
        flex-direction: column;
        align-items: center;
        border-radius: 2em;
    }

    .actionContainer {
        display: flex;
        justify-content: space-around;
        margin-top: 3em;
        gap: 2em;
    }

    .nextButton {
        padding: 0.5em 1.5em;
        border-radius: 0.5em;
        color: white;
        font-weight: bold;
        transition: background-color 0.3s ease;
    }

    .optionsContainer {
        display: flex;
        flex-direction: column;
        gap: 1em;
        width: 100%;
    }

    .option{
        padding: 1em 2em;
        border-radius: 0.5em;
        display: flex;
        align-items: center;
        justify-content: center;
        border: 3px solid transparent;
    }

    .inputAnswer {
        width: 100%;
        padding: 0.5em;
        border-radius: 0.5em;
        border: 1px solid #ccc;
        font-size: 1em;
    }
</style>
