<template>
  <v-navigation-drawer app expand-on-hover rail color="secondary">
    <v-list>
      <v-list-item
        @click="() => $router.push('/')"
        prepend-avatar="https://i.ibb.co/4wX6Lthb/Chat-GPT-Image-Jul-7-2025-10-33-02-AM-1.png"
        :subtitle=authStore.userInfos?.email
        :title=authStore.userInfos?.username
      />
    </v-list>
    <v-divider />
    <v-list density="compact" nav>
      <v-list-item
        v-for="(menu, index) in menus"
        :key="index"
        :to="menu.link"
        :prepend-icon="menu.prependIcon"
        :class="{ 'active-menu': $route.path === menu.link }"
      >
        <v-list-item-title>{{ menu.title }}</v-list-item-title>
      </v-list-item>
    </v-list>
  </v-navigation-drawer>
</template>

<script>
  import { useAuthStore } from '@/store/auth_store';
  import { getAuth } from 'firebase/auth';
  import { ref } from 'vue';

  export default {
    name: 'NavBarUser',
    setup() {
      const authStore = useAuthStore();
      const menus = ref([
        { title: 'Accueil', link: '/', prependIcon: 'mdi-home' },
        { title: 'Course', link: '/shopping_list', prependIcon: 'mdi-cart' },
        { title: 'Menus', link: '/menu', prependIcon: 'mdi-calendar' },
        { title: 'Ma cuisine', link: '/kitchen', prependIcon: 'mdi-silverware-fork-knife' },
        { title: 'Profil', link: '/profile', prependIcon: 'mdi-account' },
      ]);

      const logout = () => {
        const auth = getAuth();
        auth.signOut()
          .then(() => window.location.href = '/login')
          .catch((error) => console.error("Error signing out: ", error));
      };

      return {
        authStore,
        menus,
        logout
      };
    }
  };
</script>

<style scoped>
  .active-menu {
    color: green;
  }

  .active-menu .v-icon {
    color: green !important;
  }
</style>